@import url("https://fonts.googleapis.com/css2?family=Overpass:wght@400;700&display=swap");
* {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  color: white;
  font-family: Overpass;
}

.page {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  background-color: #121417;
  height: 100vh;
}

.container {
  background-color: #1E252F;
  margin-top: 150px;
  margin-left: 25px;
  margin-right: 25px;
  padding: 25px;
  border-radius: 15px;
}

.container h1, p, div, button {
  background-color: #1E252F;
}

.container img {
  height: 41px;
  background-color: #292C33;
  padding: 11px;
  border-radius: 20px;
}

.container h1 {
  font-size: 23px;
  margin-top: 21px;
}

.container p {
  font-size: 15px;
  color: #848B95;
  margin-top: 15px;
  margin-bottom: 25px;
}

.but-cont {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}

.but-cont button {
  height: 40px;
  width: 40px;
  color: #848B95;
  background-color: #292C33;
  padding: 13px;
  border-radius: 20px;
  border: #292C33;
  cursor: pointer;
}

.but-cont button:hover {
  background-color: #fb7413;
  color: white;
}

.submit {
  width: 100%;
  margin-top: 20px;
  background-color: #fb7413;
  border: #fb7413;
  height: 45px;
  font-size: 15px;
  border-radius: 22.5px;
  cursor: pointer;
}

.submit:hover {
  background-color: white;
  color: #fb7413;
}

.active {
  background-color: #7C8898 !important;
  color: #F9FFFF !important;
}

.inactive {
  color: #848B95;
  background-color: #292C33;
}

.second {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  background-color: #121417;
  height: 100vh;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}

.container2 {
  text-align: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding-top: 40px;
  padding-bottom: 40px;
  padding-left: 26px;
  padding-right: 26px;
  width: 85vh;
  border-radius: 10px;
  height: 360px;
}

.container2 img {
  height: 100px;
  margin-bottom: 23px;
}

.container2 h6 {
  color: #DD945F;
  background-color: #28323B;
  padding: 15px;
  font-size: 15px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin: auto;
  border-radius: 13px;
  display: block;
}

.container2 h1 {
  margin-top: 27px;
  margin-bottom: 17px;
  font-size: 24px;
}

.container2 p {
  color: #9CA3AD;
  font-size: 15px;
  padding-left: 26px;
  padding-right: 26px;
}
